import { createRouter, createWebHistory } from "vue-router";
import LoginPage from "../components/login.vue"; // 根據你的實際路徑進行修改
import HomePage from "../components//homepage.vue"; // 根據你的實際路徑進行修改

const routes = [
  {
    path: "/",
    name: "LoginPage",
    component: LoginPage,
  },
  {
    path: "/HomePage",
    name: "HomePage",
    component: HomePage,
  },

  // 可以添加更多的路由配置
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
